import React from 'react';
import Sidebar from './Sidebar';
import Footer from './Footer';
import styles from './UserPageLayout.module.css';

/**
 * –û—Å–Ω–æ–≤–Ω–æ–π layout –¥–ª—è —Å—Ç—Ä–∞–Ω–∏—Ü—ã –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
 * –°–æ–¥–µ—Ä–∂–∏—Ç –ª–µ–≤—ã–π –±–ª–æ–∫ (Wall) –∏ –ø—Ä–∞–≤—ã–π –±–ª–æ–∫ (Sidebar)
 */
const UserPageLayout = ({ children, user }) => {
  return (
    <div className={styles.pageContainer}>
      <div className={styles.contentWrapper}>
        {/* –õ–µ–≤—ã–π –±–ª–æ–∫ - Wall (max-width: 1300px) */}
        <main className={styles.mainContent}>
          {children}
        </main>

        {/* –ü—Ä–∞–≤—ã–π –±–ª–æ–∫ - Sidebar (max-width: 500px) */}
        <Sidebar user={user} />
      </div>

      {/* Footer –ø—Ä–∏–∂–∞—Ç –∫ –Ω–∏–∑—É */}
      <Footer />
    </div>
  );
};

export default UserPageLayout;
