import React from 'react';
import styles from './UserAvatar.module.css';

/**
 * –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –∞–≤–∞—Ç–∞—Ä–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
 * –û—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –∏–ª–∏ placeholder —Å –ø–µ—Ä–≤–æ–π –±—É–∫–≤–æ–π –∏–º–µ–Ω–∏
 */
const UserAvatar = ({ user, size = 'medium', className = '' }) => {
  const sizeClass = styles[`avatar-${size}`];

  return (
    <div className={`${styles.avatarContainer} ${sizeClass} ${className}`}>
      {user?.avatarUrl ? (
        <img
          src={user.avatarUrl}
          alt={user.displayName || '–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å'}
          className={styles.avatarImage}
        />
      ) : (
        <div className={styles.avatarPlaceholder}>
          {user?.displayName?.charAt(0).toUpperCase() || '?'}
        </div>
      )}
    </div>
  );
};

export default UserAvatar;
